import { Component } from "react";
import './profile.css';
import avatar from '../../assets/images/avatar_1.png'

export default class Profile extends Component {
    level = 29;
    achivment = "        - ";
    name = " ";
    description = "C#    \n" +
        "   ";
    profile_avatar = "";

    render() {
        return(
            <div className="profiles_block">
                <div className="profile_area">
                    <img src={avatar} alt="profile avatar" width="240px" height="240px"/>
                </div>
                <p className="title_area">{this.name}</p>
                <p className="description_area">{this.description}</p>
                <div className="level_area">:
                    <span>{this.level}</span>
                </div>
                <div className="achivment_area">:
                    <p>{this.achivment}</p>
                </div>
                <div className="edit_button_area">
                    <button className="edit_button"> </button>
                </div>
            </div>
        );
    }
}